.container-footer{
    width: 100%;
    height: 100%;
    background-color: var(--white);
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 20px;
}
.title-footer{
    font-family: 'Roboto', sans-serif;
    color: var(--cyan);
    font-size: 3.5vw;
    text-align: center;
    font-weight: 500;
}
.title-email{
    font-family: 'Roboto', sans-serif;
    color: var(--cyan);
    font-size: 2vw;
    text-align: center;
}
@media (max-width:920px) {
    .title-footer{
        font-size: 7.5vw;
    }
    .title-email {
        font-size: 4vw;
    }
}